<template>
  <transition id="search" name="fade" class="wrapper">
    <div class="search" v-show="showDetail">
      <div class="topctrl">
        <div class="back" @click="showToggle(1)">
          <i class="fa fa-angle-left fa-2x"></i>
        </div>
        <input class="searchInput" v-model="searchWorld" placeholder="" type="text">
        <i class="el-icon-close back" @click="searchWorld=''" v-if="searchWorld!=''"></i>
      </div>
      <div class="resultsBox">
        <div class="HS-title"></div>
        <div class="hotSearchBox">
          <div
            class="cellHS"
            v-for="(item,index) of hotList"
            :key="index"
            @click="searchWorld=item.first"
          >{{item.first}}</div>
        </div>
      </div>
      <div class="resultsBox">
        <div class="HS-title"></div>
        <div class="hotSearchBox">
          <div
            class="cellHS"
            v-for="(item,index) of searchHistory"
            v-show="index!=0"
            :key="index"
            @click="searchWorld=item"
          >{{item}}</div>
        </div>
      </div>
      <div class="SearchResults" v-for="(item,index) of searchList" :key="index" @click="playSong(item.id,item.name,item.artists[0].name,item.album.blurPicUrl)">
        <div class="searchName">{{item.name}}</div>
        <div>{{item.artists[0].name}}</div>
      </div>
    </div>
  </transition>
</template>

<script>
import BScroll from 'better-scroll'
const debounce = (func, wait) => {
  let timeout = "";
  return v => {
    if (timeout) {
      clearTimeout(timeout);
    }
    timeout = setTimeout(() => {
      func(v);
    }, wait);
  };
};

export default {
  // mounted() {
  //   this.getData();
  // },
  data() {
    return {
      hotList: [],
      searchWorld: "",
      searchList: [],
      showDetail: false,
      searchHistory: []
    };
  },
  watch: {
    searchWorld: function() {
      this.debounceSearch(this);
    }
  },
  methods: {
    getData: function() {
      this.$axios.get(this.api1 + "/search/hot").then(re => {
        this.searchHistory = localStorage.getItem('searchHistory');
        this.searchWorld=''
        this.hotList = re.data.result.hots;
        this.btScroll();
      });
    },
    sollorder() {
      this.detailWrapper = new BScroll(this.$refs.wrapper, {
        click: true // false
      });
    },
    debounceSearch: debounce(function(e) {
      if (e.searchWorld != "") {
        e.getHot();
      } else {
        e.searchList = [];
      }
    }, 400),
    getHot: function() {
      this.$axios.get(this.api1 + "/search?keywords=" + this.searchWorld)
      .then(re => {
        this.searchList = re.data.result.songs;
        
      });
      this.searchHistory.push(this.searchWorld);
        localStorage.setItem('searchHistory',this.searchHistory)
    }
  }
};
</script>

<style scoped>
.fade-enter-active,
.fade-leave-active {
  transition: all 0.2s ease;
  transform: translate3d(0, 0, 0);
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  transition: all 0.4s ease;
  transform: translate3d(100%, 0, 0);
}
#search,
.search {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  z-index: 50;
  background: #ffff;
}
.topctrl {
  background: #ffb5b0;
  display: flex;
  top: 0;
  width: 100%;
}
/* .resultsBox:first-child{
  margin-top: 44px;
} */
.searchInput {
  background: transparent;
  border: none;
  border-bottom: 1px solid #dddad3;
  color: #fff;
  border-radius: 0;
  width: calc(100% - 88px);
  height: 28px;
  margin: auto 0;
}
.searchInput::-webkit-input-placeholder {
  color: #fff;
}
.searchInput:focus {
  outline: none;
}
.HS-title {
  text-align: left;
  padding: 10px 0 8px 14px;
  font-size: 12px;
}
.hotSearchBox {
  display: flex;
  flex-wrap: wrap;
  padding: 0 10px;
}
.cellHS {
  border: 1px solid #f3f3f3;
  border-radius: 50px;
  margin: 4px;
  font-size: 12px;
  padding: 3px 10px;
  background: #f3f3f3;
}
.SearchResults {
  border-bottom: 1px solid #e4e4e4;
  width: 100%;
  text-align: left;
  height: 40px;
  font-size: 10px;
  line-height: 20px;
  padding: 4px 10px;
}
.searchName {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 16px;
}
</style>


