<template>
  <div>
    ccvvvvvv
    <div
      id="app"
      class="box"
      v-tap="vuetouch"
      v-longtap="{fn:vuetouch,name:''}"
      v-swipeleft="{fn:vuetouch,name:''}"
      v-swiperight="{fn:vuetouch,name:''}"
      v-swipeup="{fn:vuetouch,name:''}"
      v-swipedown="{fn:vuetouch,name:''}"
    >{{ name }}</div>
  </div>
</template>

<script>
export default {
 el:"#app",
			    data:{
			        name:""
			    },
			    methods:{
			        vuetouch:function(s,e){
			            this.name=s.name;
			        }
			    }
};
</script>
<style scoped>
</style>


